import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';
import { AuthProvider } from '../../providers/auth/auth';
import { Observable } from 'rxjs';
import { Livestream, Collections } from '../../app/app.models';
import { AngularFirestore } from 'angularfire2/firestore';

@IonicPage()
@Component({
  selector: 'page-asesorias',
  templateUrl: 'asesorias.html',
})
export class AsesoriasPage {

  public asesorias$: Observable<Livestream[]> = this.afs.collection<Livestream>(Collections.LIVESTREAM).valueChanges()

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public back: BackProvider,
    private modal: ModalController,
    public auth: AuthProvider,
    private afs: AngularFirestore
  ) {
  }

  ionViewDidLoad() {
    
  }

  openAdd() {

    const m = this.modal.create('AsesoriaAddPage')
    m.present()

    m.onDidDismiss(data => {
      if (data) this.ionViewDidLoad()
    })

  }

}



